@import url(https://fonts.googleapis.com/css?family=Reenie+Beanie);
@import url(https://fonts.googleapis.com/css?family=Reenie+Beanie|Short+Stack);
body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}



.App {
  text-align: center;
}


#hr1 {
  height:0px;
  border-width:20px;
  background-color:#76CBC4;
  width:100%;
}

#hr2 {
  width:100%;
  height:0px;
  border-width: 20px;
  border-color:#75818F;

}

.form{
    width: 650px;
    display: flex;
    flex-direction: column;
    background-size:cover;
}

@media only screen and (max-width: 600px) {
    .center{
        display: flex;
        flex-direction: column;
        width: auto;
  }

}

.quote {
    color: black;
    font-family: 'Reenie Beanie', cursive;
    font-size: 25px;
}

.author {
    align-self: flex-end;
    color: black;
    font-family: 'Reenie Beanie', cursive;

}

.font {
    font-family: 'Reenie Beanie', cursive;
    font-size: 20px;
    margin-top: 5px;
    margin-bottom: 1px;
}

.testimonials{
  width:100%;
  height:auto;
  align-items:center;
  background-color:#76CBC4;
}

#testimonialsul {
  width:100%;
  height:auto;
  display:flex;
  justify-content:center;

  align-items:center;
  background-color:#75818F;
}

.testimonial {
  list-style-type: none;
  width:100%;
  height:auto;
  border: 10px;
  padding:10px;
  margin: 10px 5px 20px 5px;
  background-color:#E6B3C6;
}

#about {
  margin-top:5px;
  width:100%;
  border: 1px solid black;
}

.link:focus {
    outline: 0;
    text-decoration: none;
    
}


.link:focus {
    outline: 0;
    text-decoration: none;
    
}

.navbar{

    height: 60px;



}
#menu {
    background-color: #76CBC4;
    position: -webkit-sticky;
    position: sticky;
    height: 60px;
    border-radius: 10px;
    margin-top: 0;
    padding: 20px;
    display: flex;
    list-style-type: none;
    width: 100%;
}



.Logo{
    flex-grow: 2;
    margin: auto;
}

.Login {
    flex-grow: 0;
    margin-right: 20px;
    font-family: 'Short Stack', cursive;
}

.Register {
    margin-right: 20px;
    font-family: 'Short Stack', cursive;
}

.Home{
    margin-left: 20px;
    font-family: 'Short Stack', cursive;
}


.Home:hover, .Login:hover, .Register:hover, .filteredresults:hover{
    cursor: pointer;
    border-bottom: 3px solid;
    border-bottom-color: #f25076;

}

a {
    text-decoration: none;
}

#FilteredResultsPage{
  /* height:100%;
  background-color:orange;
  width:100%; */
}

#filteredresultsul {
  display:grid | inline-grid;
  list-style-type: none;
  height:200px;
  background-color: orange;
  border: 1px solid black;

}

.resultitem {
    display:flex;
    flex-direction:row;
    width:40%;

    padding:10px;
    margin:10px;
    border: 1px solid black;
}

